.slider {

  // Labels
  // --------------------------------------------------
  &__label-container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  // Numbers
  // --------------------------------------------------
  &__number-container {
    position: relative;
    min-height: 1rem;
    // Indent half the width of the range slider handle either side
    margin: 0 1rem;

    .dir-rtl & {
      .transform(rotateY(180deg));
    }
  }

  &__number,
  &__number-selection,
  &__number-model-answer {
    position: absolute;
    top: 0;
    .transform(translateX(-50%));

    .dir-rtl & {
      .transform(translateX(-50%) rotateY(180deg));
    }
  }

  // Scale
  // --------------------------------------------------
  &__scale-container {
    position: relative;
    height: 1px;
    // Indent half the width of the range slider handle either side
    margin: 0.5rem 1rem 1.5rem;
    background-color: blue;
  }

  &__scale-notch {
    position: absolute;
    top: -5px;
    height: 10px;
    width: 1px;
    .transform(translateX(-50%));
    background-color: red;
  }

  &__scale-notch-start {
    left: 0;
  }

  &__scale-notch-end {
    left: 100%;
  }

  // Slider bar
  // --------------------------------------------------
  &__item {
    position: relative;

    input {
      width: 100%;
      appearance: none;
      background-color: transparent;

      &:not([disabled]) {
        cursor: pointer;
      }
    }

    input[type=range]::-webkit-slider-thumb {
      appearance: none;
      margin-top: -1rem;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
      border: 0;
      height: 2rem;
      width: 2rem;
      border-radius: 50%;
      background: white;
      cursor: pointer;
    }

    input[type=range]::-moz-range-thumb {
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
      border: 0;
      height: 2rem;
      width: 2rem;
      border-radius: 50%;
      background: white;
      cursor: pointer;
    }

    input[type=range]::-moz-range-track {
      width: 100%;
      height: 0.375rem;
      box-shadow: none;
      background: #dddddd;
      border-radius: 10px;
    }

    input[type=range]::-webkit-slider-runnable-track {
      appearance: none;
      width: 100%;
      height: 0.375rem;
      box-shadow: none;
      background: #dddddd;
      border-radius: 10px;
    }
  }

  // Class to show whether the users selection is in/correct
  // --------------------------------------------------
  &__widget.show-user-answer.is-correct &__number-selection {
    background-color: @validation-success;
    color: @validation-success-inverted;
  }

  &__widget.show-user-answer.is-correct .rangeslider__fill {
    background-color: @validation-success;
  }

  &__widget.show-user-answer.is-incorrect &__number-selection {
    background-color: @validation-error;
    color: @validation-error-inverted;
  }

  &__widget.show-user-answer.is-incorrect .rangeslider__fill {
    background-color: @validation-error;
  }

  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer &__number-selection {
    .u-display-none;
  }

  &__widget.show-correct-answer.is-correct &__number-model-answer,
  &__widget.show-correct-answer.is-incorrect &__number-model-answer {
    background-color: @validation-success;
    color: @validation-success-inverted;
  }

  &__widget.show-correct-answer.is-correct .rangeslider__fill,
  &__widget.show-correct-answer.is-incorrect .rangeslider__fill {
    background-color: @validation-success;
  }
}

// Rangeslider overrides
// --------------------------------------------------
.rangeslider {
  &--horizontal {
    height: 0.375rem;
  }

  &--horizontal &__handle {
    top: -0.875rem;
  }

  &__handle {
    height: 2rem;
    width: 2rem;
  }

  &__handle:after {
    height: 1rem;
    width: 1rem;
  }

  .slider__widget.is-disabled &__handle {
    cursor: default;
  }
}
